<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <link rel="stylesheet" type="text/css" href="'https://cdn.jsdelivr.net/npm/vuetify@3.0.1/dist/vuetify.min.css'">
    <nav id="navbar" class="px-6">
        <div class="nav-section uppered letter-space-1">
            <a href="/" class="logo-text-link">Nova</a>
        </div>
        <div class="nav-end d-flex">
            <span id="horloge" class="px-5"></span>
            <div v-if='$store.state.user && $store.state.user.role == 1'>
                <v-btn ligth color="secondary" elevation="7" size="x-small" v-on:click="goToDashboard" >dasboard</v-btn>
            </div>
        </div>

    </nav>
</template>

<script>
export default{

    mounted(){
        this.showDate()
    },

    methods: {
        goToDashboard: function(){
            window.location.href = '/dash'
        },

        showDate: function(){
            let date = new Date(),
                   h = date.getHours(),
                   m = date.getMinutes(),
                   s = date.getSeconds()
            
            if( h < 10 ){ h = '0' + h; }
            if( m < 10 ){ m = '0' + m; }
            // if( s < 10 ){ s = '0' + s; }
            var time = h + ':' + m 
            document.getElementById('horloge').innerHTML = time;
            this.refresh();
        },

        refresh: function(){
            setTimeout(this.showDate, 1000)
        }
    }
}

</script>
